.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    padding: 15px;
    font-style: italic;
    font-size: 17px;
}

.dialogsItems .active {
    color: gold;
}

.messages {
    padding: 10px;
    color: white;
}

.messages .message {
    color: white;
}

